html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Red Hat Display", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

a {
  color: #3829e0;
  font-weight: 700;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 700;
}

h1 {
  font-size: 2rem;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

body {
  background-image: url("../img/pattern-background-mobile.svg");
  background-color: #e0e8ff;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top center;
}
@media (min-width: 768px) {
  body {
    background-image: url("../img/pattern-background-desktop.svg");
    background-color: #e0e8ff;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: top center;
  }
}

.card {
  max-width: 35rem;
  margin: 10rem auto;
  box-shadow: 0 4rem 8rem #e0e8ff;
}
@media (min-width: 768px) {
  .card {
    max-width: 42rem;
    margin: 15rem auto;
  }
}
.card__image {
  width: 100%;
  overflow: hidden;
  border-top-left-radius: 2rem;
  border-top-right-radius: 2rem;
}
.card__info {
  background-color: #FFFFFF;
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
  padding: 2rem 4rem;
  text-align: center;
}
.card__heading {
  font-size: 2.5rem;
  font-weight: 900;
  color: #1f2f56;
}
.card__description {
  color: #7280a7;
  font-weight: 500;
}
.card__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  padding: 1rem 0;
  margin: 2.5rem 0;
  background-color: #f5f7ff;
  border-radius: 1.5rem;
  position: relative;
}
.card__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 4rem;
}
.card__text::before {
  content: "";
  position: absolute;
  display: inline-block;
  width: 5rem;
  height: 5rem;
  background-image: url("../img/icon-music.svg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  left: 0.5rem;
}
.card__text:nth-child(1) {
  grid-column: 1/3;
}
.card__text:nth-child(2) {
  grid-column: 3/4;
}
.card__plan, .card__price {
  margin: 0;
}
.card__plan {
  font-weight: 900;
  color: #263969;
}
.card__option {
  text-align: right;
  padding-right: 2rem;
}
.card__link:hover {
  text-decoration: none;
  color: #6255e7;
}
.card__btn {
  display: block;
  background-color: #3829e0;
  color: #FFFFFF;
  padding: 2rem;
  border-radius: 1.5rem;
  text-decoration: none;
  box-shadow: 0 2rem 8rem #d2daef;
}
.card__btn:hover {
  background-color: #6255e7;
}
.card__btn-cancel {
  padding: 3.5rem 0 2rem 0;
  display: block;
  color: #7280a7;
  text-decoration: none;
}
.card__btn-cancel:hover {
  color: #1f2f56;
}