@use 'variables' as v; 

html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after{
    box-sizing: inherit;
}  
body{
    font-family: v.$fuentePrincipal;
    font-size: 1.6rem;
    font-weight: 300;
}
.container{
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
img{
    max-width: 100%;
    display: block;
}
a{
    color: v.$brightBlue;
    font-weight: 700;
}
ul{
    padding:0;
    list-style: none;
    margin: 0;
}